<template>
    <div>
        <el-form ref="form" :model="form" label-width="150px" :rules="rules">
    
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="公司名称" prop="company_name">
                        <el-input v-model="form.company_name" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="是否有反担保资质" prop="">
                        <el-radio-group v-model="form.is_assure" :disabled="isDisabled()">
                            <el-radio label="1">是</el-radio>
                            <el-radio label="2">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="联系人" prop="contact">
                        <el-input v-model="form.contact" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="移动电话" prop="mobile">
                        <el-input v-model="form.mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="固定电话">
                        <el-input v-model="form.fixed_telephone" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="公司地址" prop="address">
                        <el-input v-model="form.address" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急联系人" prop="emer_contact">
                        <el-input v-model="form.emer_contact" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急移动电话" prop="emer_mobile">
                        <el-input v-model="form.emer_mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急联系人地址" prop="emer_address">
                        <el-input v-model="form.emer_address" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="公司法人" prop="corporate">
                        <el-input v-model="form.corporate" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="公司实际控制人">
                        <el-input v-model="form.actual_controller" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　认证信息" name="2" v-if="pageType != 'edit'">
                    
                    <el-form-item label="营业执照照片" prop="url">
                        <el-upload
                            :disabled="isDisabled()"
                            class="avatar-uploader"
                            :data="{type:5}"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.url" :src="form.url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="统一社会信用代码" prop="credit_code">
                        <el-input v-model="form.credit_code" @blur="handleVerifyCode" v-loading="loadingCreditCode" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　名下车辆列表（公司体系下所有车辆）" name="3" v-if="pageType == 'view'">
                    
                    <el-table :data="form.car_info" border class="table" ref="multipleTable">
                        <el-table-column prop="plate_number" label="车牌号" width="150">
                        </el-table-column>
                        <el-table-column prop="vin_number" label="vin" width="120">
                        </el-table-column>
                        <el-table-column prop="car_info" label="车辆信息">
                        </el-table-column>
                        <el-table-column prop="band_name" label="购车公司">
                        </el-table-column>
                        <el-table-column prop="bus_mech_name" label="挂靠公司">
                        </el-table-column>
                        <el-table-column prop="create_time" label="加入时间">
                        </el-table-column>
                    </el-table>

                </el-collapse-item>

                <el-collapse-item title="　征信信息" name="4" v-if="pageType == 'view'">
                    
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="历史贷款">
                                {{form.credit_info.history_num}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="贷款总额">
                                {{form.credit_info.total_loan_amount}}
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="贷款人数">
                                {{form.credit_info.overdue_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="是否黑名单">
                                {{form.credit_info.status == 3 ? '是' : '否'}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　信贷信息" name="5" v-if="pageType == 'view'">
                    
                    <el-table :data="form.loan_info" border class="table" ref="multipleTable">
                        <el-table-column prop="contract_num" label="合同号" width="150">
                        </el-table-column>
                        <el-table-column prop="total_loan_amount" label="贷款金额" width="120">
                        </el-table-column>
                        <el-table-column label="贷款性质">
                            <template slot-scope="{row}">
                                {{row.credit_type == 1 ? '助贷' : '自贷'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="already_pay_count" label="账期信息">
                        </el-table-column>
                        <el-table-column label="是否结清">
                            <template slot-scope="{row}">
                                {{row.is_give == 2 ? '是' : '否'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="plate_number" label="车牌号">
                        </el-table-column>
                        <el-table-column label="是否挂靠">
                            <template slot-scope="{row}">
                                {{row.cp_affiliat_name == 1 ? '是' : '否'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="user_id" label="创建人员">
                        </el-table-column>
                        <el-table-column prop="create_time" label="创建时间">
                        </el-table-column>
                    </el-table>

                </el-collapse-item>


            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            let validateCreditCode = (rule, value, callback)=>{
                if (!value) {
                    return callback(new Error('请输入统一社会信用代码'));
                }else{
                    this.loadingCreditCode = true
                    API.apiCompanyFileVerifyCode({credit_code: this.form.credit_code}).then(res => {
                        if(res.data){
                            callback();
                        }else{
                            callback(new Error('已经有了'));
                        }
                        this.loadingCreditCode = false
                    })
                }
            }
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                loadingCreditCode: false,
                pageType: 'add',
                form: {
                    actual_controller: "",
                    address: "",
                    car_info: [],
                    company_name: "",
                    contact: "",
                    corporate: "",
                    credit_code: "",
                    credit_info: {},
                    emer_address: "",
                    emer_contact: "",
                    emer_mobile: "",
                    fixed_telephone: "",
                    is_assure: "2",
                    loan_info: [],
                    mobile: "",
                    url: "",
                },
                rules: {
                    company_name: [
                        { required: true, message: '请输入', trigger: 'blur' }
                    ],
                    actual_controller: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    address: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    car_info: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    company_name: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    contact: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    corporate: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    credit_code: [
                        {required: true, validator:validateCreditCode, trigger: 'blur'}
                    ],
                    credit_info: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    emer_address: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    emer_contact: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    emer_mobile: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    fixed_telephone: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    is_assure: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    loan_info: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    mobile: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    url: [
                        {required: true, message: '请上传图片', trigger: 'blur'}
                    ]
                }
            }
        },
        mounted(){
            this.checkPath()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.getFormData();
                    this.pageType = 'view';
                }
            },
            getFormData(){
                API.apiCompanyFileView({id: this.$route.query.id, operation_uniqid: this.$route.query.operation_uniqid}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            handleUploadSuccess(res, file, fileList){
                this.form.url = res.data.url
                this.form.credit_code = res.data.register_number
            },
            handleBeforeUpload(){

            },
            handleVerifyCode(){
                
            },
            handleSubmit() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if(this.pageType == 'add'){
                            API.apiCompanyFileAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiCompanyFileEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    } else {
                        return false;
                    }
                });
                
                
                
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>